.friends {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;        /*   */
  }
  &::-webkit-scrollbar-thumb {
    background-color: #B6ADAE;    /*   */
    border-radius: 2px;       /*   */
  }
  
}